export default {
	data() {
		return {
			data:{},
			swiperheight: 667,
			height: "",
			background: {
				backgroundImage: "linear-gradient(90deg, #54C3F1, #00ABEB)",
			},
			show: false,
			action: 'localhost', // 演示地址
			maxcount: 1,
			type:"家政保洁",
			list: [{
					value: '家政保洁',
					label: '家政保洁'
				},
				{
					value: '开荒保洁',
					label: '开荒保洁'
				}
			],
			showUploadList: false,
			// 如果将某个ref的组件实例赋值给data中的变量，在小程序中会因为循环引用而报错
			// 这里直接获取内部的lists变量即可
			lists: [{
					url: '../../static/logo.png', // 预览图片的地址
					error: false, // 上传失败，此值为true
					progress: 100, // 0-100之间的值
				}

			],
			src: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2119_s.jpg',
			text: '无头像',
			count: 5,
			value: 4
		}
	},
	onLoad(event) {
		console.log("aaa");
		//设置容器高度
		uni.getSystemInfo({
			success: (res) => {
				console.log(res.windowHeight);
				let height = res.windowHeight - uni.upx2px(68);
				this.swiperheight = height;
			}
		});
		// TODO 后面把参数名替换成 payload
		
		console.log(event.userinfo );
		const payload = event.userinfo || event.payload;
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.data= JSON.parse(decodeURIComponent(payload));
			
			console.log(this.data);
		} catch (error) {
			this.data = JSON.parse(payload);
			console.log(this.data);
		}
	},
	methods: {
		submit() {
			uni.showLoading({
				title: '保存中'
			});
			
			setTimeout(function() {
				uni.hideLoading();
				uni.showToast({
					title:"保存成功"
				})
			}, 2000);
			
		},
		// 类型选择
		confirm(e) {
			console.log(e);
			this.type = e[0].label
			
		}
	},
	onReady() {
		// 得到整个组件对象，内部图片列表变量为"lists"
		this.lists = this.$refs.uUpload.lists;
	},
	//自定义分享页面
	onShareAppMessage(e){
		return {
			title: this.$overShare.title,
			path: this.$overShare.path,
			imageUrl:this.$overShare.imageUrl,
			
		}
	}
}
